const state = {
  newarr:[]
}
const getters = {
  getarr(state){
    return state.newarr
  }
}
const mutations = {
  mutationarr(state,items){
    state.newarr.push(items)
  }
}
const actions = {
  actionarr(context,item){
    context.commit('mutationarr',item)
  }
}
export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



