const state = {
  listarr:[]
}
const getters = {
  getlist(state){
    return state.listarr
  }
}
const mutations = {
  mutationlist(state,items){//左侧列表展示
    state.listarr = [];
    for(let i = 0;i<items.length;i++){
      state.listarr.push(items[i])
    }
    console.log(items)
  }
}
const actions = {
  actionlist(context,item){
    context.commit('mutationlist',item)
  }
}
export default{
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



