// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'//引入路由
import ElementUI from 'element-ui';//element插件
import 'element-ui/lib/theme-chalk/index.css';//element的css
import Router from 'vue-router';//路由
import './assets/css/iconfont.css';//icon图标
import store from './store';//vuex
import axios from 'axios'
import { Loading,Message } from 'element-ui'

Vue.prototype.$axios = axios;
axios.defaults.baseURL = 'http://192.168.43.133:8080';//默认ip地址
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8';//post请求的头部信息
axios.defaults.timeout = 50000;//响应超时时间

var loadinginstace//声名请求接口加载状态
axios.interceptors.request.use(config=>{//请求拦截
  loadinginstace = Loading.service({ lock:true,text:'Loding',spinner:'el-icon-loading',background:'rgba(0,0,0,0.7)'})//加载loding
  return config;
},error=>{
  loadinginstace.close();//关闭loding
  Message.error({
    message:'网络不给力，请稍后再试。'
  })
  return Promise.reject(error)
})

axios.interceptors.response.use(data=>{//响应拦截
  loadinginstace.close();
  return data
},error=>{
  loadinginstace.close();
  Message.error({
    message:'网络不给力，请稍后再试。'
  })
  return Promise.reject(error)
})

const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {//解决重复点击路由报错问题
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(ElementUI)

router.beforeEach((to,form,next)=>{
  // console.log(to)
  if(to.meta != ''){
    document.title = to.meta;
  }
  if(to.name == "Index"){
      console.log(store.state.list.listarr)
  }
  // console.log(form)
  next();
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



