var router = {routes: [
  {
    path:'/',
    name:'Login',
    component:() => import('../components/login.vue'),
  },
  {
    path:'/add',
    name:'add',
    component:() => import('../components/projectManagement/add.vue'),
    meta:'新增上级项目'
  },
  {
    path:'/addLeve',
    name:'addLeve',
    component:() => import('../components/projectManagement/addLeve.vue'),
    meta:'新增本级项目'
  },
  {
    path: '/index',
    name: 'Index',
    component: () => import('../components/index.vue'),
    // redirect:'/right',//默认展示路由重定向
    children:[
      {
        path:'/right',
        name:'right',
        component:() => import('../components/right.vue')
      },
      {
        path:'/right2',
        name:'right2',
        component:()=>import('../components/right2.vue')
      },
      {
        path:'/addProject',
        name:'addProject',
        component:() => import('../components/projectManagement/addProject.vue'),//新增上级项目
        meta:'上级项目'
      },
      {
        path:'/addLeveProject',
        name:'addLeveProject',
        component:() => import('../components/projectManagement/addLeveProject.vue'),//新增本机项目
        meta:'本级项目'
      },
      {
        path:'/addTendering',
        name:'addTendering',
        component:() => import('../components/tenderingManagement/addTendering.vue'),//新增招标
        meta:'新增招标'
      },
      {
        path:'/subTend',
        name:'subTend',
        component:() => import('../components/tenderingManagement/subTend.vue'),//新增招标提交
        meta:'新增招标提交'
      },
      {
        path:'/approvalTend',
        name:'approvalTend',
        component:() => import('../components/tenderingManagement/approvalTend.vue'),//新增招标审批
        meta:'新增招标审批'
      },
      {
        path:'/addContract',
        name:'addContract',
        component: ()=> import('../components/contractManagement/addContract.vue'),//新增合同
        meta:'新增合同'
      },
      {
        path:'/subContract',
        name:'subContract',
        component:() => import('../components/contractManagement/subContract.vue'),//新增合同提交
        meta:'新增合同提交'
      },
      {
        path:'/approvalContract',
        name:'approvalContract',
        component:() => import('../components/contractManagement/approvalContract.vue'),//新增合同审批
        meta:'新增合同审批'
      },
      {
        path:'/addCompletion',
        name:'addCompletion',
        component:() => import('../components/completionAcceptance/addCompletion.vue'),//新增竣工验收
        meta:'新增竣工验收'
      },
      {
        path:'/subCompletion',
        name:'subCompletion',
        component:() => import('../components/completionAcceptance/subCompletion.vue'),//新增竣工验收提交
        meta:'新增竣工验收提交'
      },
      {
        path:'/approvalCompletion',
        name:'approvalCompletion',
        component:() => import('../components/completionAcceptance/approvalCompletion.vue'),//新增竣工验收审批
        meta:'新增竣工验收审批'
      }
    ]
  },
]}
export default router;//导出路由文件



